body{
    background-color: #f7faff;
    font-family: 'Open Sans', sans-serif;
    /* background: linear-gradient(45deg,#e0eafc, #cfdef3) */
}

/* ================ CLASS ================ */
.main-container{
    display: flex;
}

.backlog, .todo, .done, .complete{
    display: flex;
    flex-direction: column;
}

.main-container .card{
    border: 0;
    border-radius: .25rem;
}

.main-container .card-body{
    padding: 10px;
    border-radius: .25rem;
    overflow: hidden;
}

.main-container .card-background{
    background-color:#8890dd ;
    padding: 10px 7px;

}

.backlog .card-body{
    background-color: #ffe8ff;
}

.todo .card-body{
    background-color: #fff0d5;
}

.doing .card-body{
    background-color: #d9e4ff;
}

.complete .card-body{
    background-color: #caeffa;
}

.card-title, .card-text, .card-heading{
    color: #191c22;
}

.add-form{
    display: flex;
    align-items: center;
    justify-content: center; 
}

.navbar{
    background: rgb(154,168,251);
    background: linear-gradient(90deg, rgba(154,168,251,1) 0%, rgba(142,170,250,1) 35%, rgba(127,174,250,1) 100%);
}

 .backlog{
    background-color: #ba8096
}

 .todo{
    background-color: #ddb583;
}
 .doing{
    background-color: #8ca9d4
}
 .complete{
    background-color: #93c5b9;
}
